// In the search component's template
<template>
  <div class="tem">
    <div>
      <div class="box">
        <div class="search">
          <h3>관심있는 멸종위기 야생생물을 찾아보세요.</h3>
          <label class="hidden" for="title">생물검색 </label>
          <div class="valueBox">
            <input id="title" type="text" v-model="value" />
            <span><button @click="search()">검색</button></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// In the search component's script
import jdata from "../species.json";

export default {
  props: ["species"],
  data() {
    return {
      message: false,
      msgtext: "검색 데이터가 없습니다",
      value: "",
    };
  },
  methods: {
    search: function () {
      let text = this.value;
      if (text) {
        let results = jdata.filter(function (element) {
          return element.대표국명.includes(text);
        });
        this.message = true;
        this.msgtext = results.length == 0 ? "검색 데이터가 없습니다" : results.length + "개가 검색되었습니다";
        this.$emit("search-results", results);
        this.$emit("search-results-length", results.length);
      } else {
        alert("검색어를 입력해주세요!");
      }
    },
  },
};
</script>

