<template>
  <div class="tem">

    <ul class="gradeButtons">
      <li><button @click="whole()" >전체</button></li>
      <li><button @click="animalGrade()">1급</button></li>
      <li><button @click="animalGrade2()">2급</button></li>

    </ul>
    <!-- <span class="resultText" v-show="message">{{ msgtext }}</span> -->

    <ul>
      <li v-for="(group, index) in groups" :key="group" @mouseover="hovered = index" @mouseleave="hovered = null">
        <button @click="grouping(index)" >
          <div class="imgBox" :class="{ 'selected': selected === 'all' || selected === index  }">
            <img :src="hovered === index ? require('../assets/icon/icon_jong' + (index + 1) + 'on.png') : require('../assets/icon/icon_jong' + (index + 1) + '.png')" alt="group" />
          </div>
          <span> {{ group }}</span>
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
import jdata from "../species.json";
// const json = require('../species.json');
let results = "";
let text = "";
export default {
  data() {
    return {
      selected: null,
      message: false,
      msgtext: "검색 데이터가 없습니다",
      hovered: null,
      groups: ["포유류", "조류", "파충류", "양서류", "어류", "곤충류", "무척추동물", "육상식물", "해조식물", "고등균류"],
      species: jdata,
      value: ["포유류", "조류", "파충류", "양서류", "어류", "곤충류", "무척추동물", "육상식물", "해조식물", "고등균류"],
      animalG: ["일급", "이급"],
    };
  },
  methods: {
    grouping: function (index) {
      text = this.value[index];
      this.selected = index;

      //   console.log(text)
      if (text) {
        results = jdata.filter(function (element) {
          return element.분류군.includes(text);
        });
        this.message = true;
        this.msgtext = results.length == 0 ? "검색 데이터가 없습니다" : results.length + "개가 검색되었습니다";
        this.$emit("search-results", results);
        this.$emit("search-results-length", results.length);

      } else {
        alert("검색어를 입력해주세요!");
      }
    },
    whole: function () {
      this.selected = "all";
      results = jdata;
      this.message = true;
      this.msgtext = `${this.species.length}개가 검색되었습니다`;
      this.$emit("search-results", results);
      this.$emit("search-results-length", results.length);

    },
    animalGrade: function () {
      this.selected = null;

      text = this.animalG[0];
      //   console.log(text)
      if (text) {
        results = jdata.filter(function (element) {
          return element.등급.includes(text);
        });
        this.message = true;
        this.msgtext = results.length == 0 ? "검색 데이터가 없습니다" : results.length + "개가 검색되었습니다";
        this.$emit("search-results", results);
        this.$emit("search-results-length", results.length);

      } else {
        alert("검색어를 입력해주세요!");
      }
    },
    animalGrade2: function () {
      this.selected = null;

      text = this.animalG[1];
      //   console.log(text)
      if (text) {
        results = jdata.filter(function (element) {
          return element.등급.includes(text);
        });
        this.message = true;
        this.msgtext = results.length == 0 ? "검색 데이터가 없습니다" : results.length + "개가 검색되었습니다";
        this.$emit("search-results", results);
        this.$emit("search-results-length", results.length);

      } else {
        alert("검색어를 입력해주세요!");
      }
    },
  },
};
</script>

