  margin-top: 20px;
  display: block;
  text-align: center;
}
img {
}
.imgBox {
  height: 7rem;
  width: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50% 50%;
}
li:hover .imgBox {
  background: linear-gradient(to bottom, #08e4e7, #2f7ccd);
  border: 2px solid #2f7ccd;

}
img {
